// Fix the theme colours sent to the utilities on non-compatible browsers
@if $compatible == 'false' {
  $temp-colors: ();
  @each $color, $value in $theme-colors {
    
    $temp-colors: map-merge((
      #{$color}: var(--colour-#{$color})
    ), $temp-colors);
  }

  $utilities: map-merge(
    $utilities,
    (
      "color": map-merge(
        map-get($utilities, "color"),
        (
          values: map-merge(
            $temp-colors,
            (
              "white": var(--colour-white),
              "body": var(--colour-body),
              "muted": var(--colour-muted),
              "reset": inherit,
            )
          )
        ),
      ),
      "background-color": map-merge(
        map-get($utilities, "background-color"),
        (
          values: map-merge(
            $temp-colors,
            (
              "white": var(--colour-white),
              "body": var(--colour-body),
              "transparent": transparent
            )
          )
        ),
      )
    )
  );
}

@each $color, $value in $theme-colors {
  
  .bg-#{$color} {

    --bg-colour: var(--colour-#{$color});

    @if map-get($non-theme-colors, #{$color}-tint) != null {
      --bg-colour-tint: var(--colour-#{$color}-tint);
      --pattern-colour: var(--colour-#{$color}-tint);
    }
    @else {
      --bg-colour-tint: var(--colour-#{$color});
    }
  }

  .colour-#{$color} {

    --colour: var(--colour-#{$color});
  }
}

// Fix body colour when inverted
.text-body { 
  
  @if $compatible == "true" {
    @include invert-colours() {

      @include var(color,--colour-white,!important);
    }
  }
}

// Remove gradient class
$utilities: map-merge(
  $utilities,
  (
    "gradient": null
  )
);
