.fade {
  @include transition($transition-fade);
  
  &:not(.show) {
    opacity: 0;
  }
}

.collapse:not(.show) {
  display: none;
  height: 0;
}

.collapse:not(.show),
.collapsing {
  position: relative;
  opacity: 1;
  overflow: hidden;
  height: 0;
}
@if $compatible == 'false' {

  .collapsing:not(.tabpanel) {
    > * {
      @include var(animation,--animation-fade-out);
    }
  }


  .collapsing.opening:not(.tabpanel) {
    > * {
      @include var(animation,--animation-fade-in);
    }
  }

  body.animate .collapse:not(.show),
  body.animate .collapsing {
    @include transition($transition-collapse);
  }
}

.no-animate {
  transition: none;
}

// Non JS fallback
body:not(.js-enabled) {
  .collapse {
    &:target {
      display: block;
      position: static;
      opacity: 1;
      height: auto;
      z-index: auto;
    }
    &[data-nonjs-show] {
      display: block;
      position: static;
      opacity: 1;
      height: auto;
      z-index: auto;
    }
  }
}