// #region Default rules
ol,
ul {
  list-style: none;
  padding-left: 0;
  padding-bottom: rem(32);
}

li
{
  list-style: none;
  position: relative; 
  line-height:inherit;
  padding-left: 0;
  padding-bottom: rem(32);

  ol, ul {
    padding-top: rem(32);
    padding-bottom: 0;
  }
}
// #endregion

// #region Un-ordered-lists
ul {
  li {
    padding-left: rem(32);

    &[class*="col-"]{

    padding-left: rem(32);
    }

    &:before {
      content: "•";
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      color: inherit;
    }

    ul {
      padding-bottom: 0;

      li:last-child {

        padding-bottom: 0;
      }
    }

    ul li:before {
      @include var(color,--colour-muted); // Re-colour the dots when nested
    }
  }
}
// #endregion

// #region Ordered list
ol
{
  counter-reset: ol;
  
  > li {
    
    padding-left: rem(32);
  
    &:before {
      counter-increment: ol;
      content: counter(ol) ". ";
      position: absolute;
      top: 0;
      left: 0;
      min-width: rem(32);
      font-weight: bold;
      display: inline-block;
      @include var(font-family,--font-heading);
    }

    ol {
      counter-reset: ol_level2;


      padding-bottom: 0;

      > li:last-child {

        padding-bottom: 0;
      }
      

      > li{
        padding-left: 2em;

        &:before {
          counter-increment: ol_level2;
          content: counter(ol) "." counter(ol_level2) ". ";
        }

        ol {
          counter-reset: ol_level3;

          > li {
            padding-left: 2.5em;
            
            &:before {
              counter-increment: ol_level3;
              content: counter(ol) "." counter(ol_level2) "." counter(ol_level3) ". ";
            }
          }
        }
      }
    }
  }
}
// #endregion

// #region Desription lists
dt {
  @extend h6;
  margin-bottom: 0;
  padding-bottom: rem(8);
}

dd {
  padding-bottom: rem(64);
}
// #endregion

// #region Custom lists
.tick-list {

  > li {
    
    list-style: none;
    
    &:before{
      content: '';
      height: rem(24);
      width: rem(24);
      display: block;
      padding: 0;
      @include var(background-color, --colour-primary);
      
            
      @if $compatible == 'true' {
      
        background-image: escape-svg($tick-red-svg);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-color: transparent!important;
      }
      @else {
        mask-image: var(--icon-tick);
        mask-size: 100% 100%;
        mask-repeat: no-repeat;
        mask-position: 0 0;
      
        -webkit-mask-image: var(--icon-tick);
        -webkit-mask-size: 100% 100%;
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-position: 0 0;
      }
    }
  }

  [class*="bg-"] & > li:before {
    @if $compatible == 'true' {
  
      background-image: escape-svg($tick-svg);
    }
    @else {
      background-color: currentColor;
    }
  }

  @include invert-colours() {
    
    > li:before {
      @if $compatible == 'true' {
    
        background-image: escape-svg($tick-white-svg);
      }
      @else {
        background-color: currentColor;
      }
    }
  }

  &.list--featured {
    > li {
    
      padding-left: rem(48);

      &:before{
        height: rem(32);
        width: rem(32);
      }
    }
  }
  
  // Colour the tick-list ticks by giving the list a background colour
  &[class*="bg-"] {
    background: none!important; // Don't want to actually change the color
  }
  &[class*="bg-"] > li:before {
    background-color: var(--bg-colour)!important;
  }
}

ol.decorative {
  > li {
    min-height: rem(32);
    padding-left: rem(48);
    padding-top: rem(4);
  }

  > li:before {
    @include var(color,--colour-heading);
    content: counter(ol);
    width: rem(32);
    height: rem(32);
    line-height: rem(30);
    @if $compatible == 'true' {
      line-height: em(30);
    }
    border: 1px solid currentColor;
    border-radius: rem(16);
    text-align: center;
    padding: 0;
  }

  @include invert-colours() {
    > li:before {
      @include var(color,--colour-white);
    }
  }
}

.list--featured {

  padding-bottom: rem(32);
  padding-top: rem(32);

  &:not([class*="list-columns"]):not([class*="row"]){

    border-top: 1px solid rgba(0,0,0,0.1);
  }

  > li {
    @extend .lead;
    padding-bottom: rem(32);
    margin-bottom: rem(32);
    border-bottom: 1px solid rgba(0,0,0,0.1);
  }
  @include invert-colours() {
    
            border-color: currentColor;
    > li {
      border-color: currentColor;
    }
  }
}

.list--featured-icons {

  > li {

    padding-right: rem(16 * 3.5);

    > .svg__wrapper {
      position: absolute;
      top: 0;
      right: 0;
      width: rem(16 * 3);
      height: rem(16 * 3);
      margin-right: 0;

      svg {
        width: 100%;
        height: 100%;
      }
    }

    @include media-breakpoint-up(md) {

        padding-right: rem(16 * 7);

        > .svg__wrapper {
          width: rem(16 * 5);
          height: rem(16 * 5);
        }
    }
  }
}

ul.list--featured:not(.tick-list):not(.show-bullets) {
  > li {
    padding-left: 0;
  }

  > li:before {
    display: none;
  }
}

// #endregion

// #region Bespoke List counters
.list-bespoke {

  counter-reset: bespoke;

  li {
    padding: 0;
    position: relative;

    &:before {
      display: none;
    }

    display: flex;
    flex-direction: row;
  }
}

.list__counter {
  counter-increment: bespoke;
  flex-grow: 0;
  flex-shrink: 0;
  display: inline-block;
  width: calc(1em + 1rem);
  color: currentColor;

  @if $compatible == 'true' {
    overflow: visible;
    height: 5rem;
  }

  &:before {
    content: counter(bespoke) ". ";
  }
}

.list__content {
  flex-grow: 1;
  flex-shrink: 1;
}
// #endregion

// #region Utility classes

.list-unstyled {
  
  > li {
    padding-left: 0;
    padding-bottom: 0;
  }

  > li:before {
    display: none;
  }
}


.list-collapsed {
  > li {
    padding-bottom: 0;
  }
}

[class*="list-columns"] {

  @include prefix(column-gap, #{rem(16)}, webkit moz);


  > li {
    -webkit-column-break-inside: avoid; // Chrome, Safari 
    page-break-inside: avoid;           // Theoretically FF 20+ 
    break-inside: avoid-column;         // IE 11 
  }

  &.list-links > li:first-child > a {

    border-top: 0;
  }
  
  &.list-links > li:first-child > a {

    border-top: 0;
  }
}

ul:not(.tick-list)[class*="list-columns"],
ol:not(.tick-list):not(.decorative)[class*="list-columns"] {
  > li {
    padding-left: 0;
  }

  > li:before {
    display: none;
  }
}

.list-columns-2 {
  @include prefix(column-count, 2, webkit moz);
}

@include media-breakpoint-up(sm) {
  .list-columns-sm-1 {
    @include prefix(column-count, 1, webkit moz);
    @include var(max-width,--max-content-width);
  }
  .list-columns-sm-2 {
    @include prefix(column-count, 2, webkit moz);
    max-width: 100%;
  }
}
@include media-breakpoint-up(md) {
  [class*="list-columns"] {

    @include prefix(column-gap, #{rem(32)}, webkit moz);
  }
  .list-columns-md-1 {
    @include prefix(column-count, 1, webkit moz);
    @include var(max-width,--max-content-width);
  }
  .list-columns-md-2 {
    @include prefix(column-count, 2, webkit moz);
    max-width: 100%;
  }
  .list-columns-md-3 {
    @include prefix(column-count, 3, webkit moz);
    max-width: 100%;
  }
}
// #endregion
