.brand {
  display: inline-block;
  font-size: 0; // Fix to stop ghost padding with things are displayed inline-block
  vertical-align: inherit;
  @include var(color,--colour-primary);
  display: inline-block;
  position: relative;
  font-size: 0; // Fix to stop ghost padding with things are displayed inline-block

  padding: rem(8);


  min-height: rem(32 + 8 + 8);
  min-width: rem(158 + 8 + 8);

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - #{rem(16)});
    height: calc(100% - #{rem(16)});
    min-height: rem(32);
    min-width: rem(158);
    @include var(fill,--colour-primary);
    stroke: none!important;

    circle {
      @include var(fill,--colour-white);
    }
  }

  @include media-breakpoint-up(sm) {

    min-height: rem(40 + 8 + 8);
    min-width: rem(198 + 8 + 8);

    svg {
      min-height: rem(40);
      min-width: rem(198);
    }
  }

  @include invert-colours(){

    &:before {
      @include var(background,--colour-primary);
    }

    svg {
      @include var(fill,--colour-white);

      circle {
        @include var(fill,--colour-primary);
      }
    }
  }

  &--stacked {

    min-height: rem(70 + 9 + 9);
    min-width: rem(83 + 8 + 8);

    padding-top: rem(9);
    padding-bottom: rem(9);

    svg {
      min-height: rem(70);
      min-width: rem(83);
    }

    @include media-breakpoint-up(sm) {

      min-height: rem(84 + 10 + 10);
      min-width: rem(103 + 8 + 8);

      padding-top: rem(10);
      padding-bottom: rem(10);

      svg {
        min-height: rem(86);
        min-width: rem(103);
      }
    }
  }

  &--digital {

    min-height: rem(44 + 8 + 8);
    min-width: rem(40 + 8 + 8);

    svg {
      min-height: rem(44);
      min-width: rem(77);
    }
  }
}

a.brand {
  &:hover,
  &:focus,
  &:active,
  &:hover:active,
  &:focus:active {
    @include var(color,--colour-primary,!important);
    text-decoration: none!important;
  }
}

