@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes slide-up {
  from { transform: translate(0, $animation_offset); }
  to { transform: translate(0,0); }
}

@keyframes slide-right {
  from { transform: translate(-100%,0); }
  to { transform: translate(0,0); }
}
@keyframes slide-right-out {
  from { transform: translate(0,0);}
  to { transform: translate(-100%,0); }
}

@keyframes slide-down {
  from { transform: translate(0, -#{$animation_offset}); }
  to { transform: translate(0,0); }
}

@keyframes slide-left {
  from { transform: translate(100%,0); }
  to { transform: translate(0,0); }
}
@keyframes slide-left-out {
  from { transform: translate(0,0); }
  to { transform: translate(100%,0); }
}

@keyframes draw-in {
  from { 
    stroke-dashoffset: calc(var(--pattern-length) - var(--pattern-offset));
    stroke-dasharray: var(--pattern-length);
  }
  to { 

    stroke-dashoffset: var(--pattern-offset);
    stroke-dasharray: var(--pattern-length);
  }
}


@keyframes draw-out {
  from { 
    stroke-dashoffset: var(--pattern-offset);
    stroke-dasharray: var(--pattern-length);
  }
  to { 
    stroke-dashoffset: calc(var(--pattern-length) - var(--pattern-offset));
    stroke-dasharray: var(--pattern-length);
  }
}


@keyframes modal-fade-up {
  from { 
    opacity: 0;
    transform: translate(-50%, calc(-50% + #{$animation_offset}));
  }
  to { 
    opacity: 1; 
    transform: translateX(-50%) translateY(-50%);
  }
}