.dynamic.updated {
  animation: var-flash 0.5s; /* IE 10+, Fx 29+ */
}


@keyframes var-flash {
  0%, 100% {
    @include var(color,--colour-body);
    background: none;
  }
  35% {
    @include var(color,--colour-secondary);
    background: rgba(255,109,184,0.1);
  }
  70% {
    @include var(color,--colour-secondary-dark);
    background: rgba(255,109,184,0.1);
  }
}