$utilities: map-merge(
  $utilities,
  (
    "font-size": null,
    "text-align": (
      responsive: true,
      property: text-align,
      class: text,
      values: (
        start: left,
        end: right,
        left: left,
        right: right,
        center: center,
      )
    ),
  )
);

.font-heading { 
  
  @include var(font-family,--font-heading);

}
strong.font-heading {
  font-weight: bold;
}

.font-weight-normal {
  font-weight: 400 !important;
}