.theme--business {
  --colour-dark: #09142E;
  --colour-dark-tint: #000005;
  --colour-secondary: #{$success};
  --colour-secondary-tint: #{$colour-success-tint};
  --colour-secondary-light: #{$colour-success-dark};
  --colour-secondary-2: #323A57;
  --colour-secondary-2-tint: #000005;

  /* Pagination */
  --bg-pagination-next: var(--colour-secondary);
  --bg-pagination-prev: var(--colour-secondary-tint);
  --colour-pagination: #{$colour-body};

  @if $compatible == 'true' {
    .bg-dark {
      background-color: #{$colour-dark-2}!important;
    }
    .text-dark {
      color: #{$colour-dark-2}!important;
    }
    .bg-secondary {
      background-color: #{$success}!important;
    }
    .text-secondary {
      color: #{$success}!important;
    }
    .bg-secondary-2 {
      background-color: #000005!important;
    }
    .text-secondary-2 {
      color: #000005!important;
    }
  }

  // Patterns
  $stroke-width-sm: 18px;
  $stroke-width-default: 24px;
  $stroke-width-lg: 21px;
  $stroke-width-xl: 20px;
  --pattern-stroke-sm: #{$stroke-width-sm};
  --pattern-stroke-default: #{$stroke-width-default};
  --pattern-stroke-lg: #{$stroke-width-lg};
  --pattern-stroke-xl: #{$stroke-width-xl};

  @if $compatible == 'true' {
    .pattern__inner svg{
      stroke-width: #{$stroke-width-default};
    }
    
    .pattern--xs .pattern__inner svg {
      stroke-width: #{$stroke-width-sm}; 
    }
    
    .pattern--sm .pattern__inner svg {
      stroke-width: #{$stroke-width-sm};
    }

    .pattern--lg .pattern__inner svg {
      stroke-width: #{$stroke-width-lg};
    }

    .pattern--xl .pattern__inner svg {
      stroke-width: #{$stroke-width-xl};
    }
    
    .pattern--xxl .pattern__inner svg {
      stroke-width: #{$stroke-width-xl};
    }
  }

  // Colour combinations
  .bg-dark#{$dark-bg-selector} {
    --colour-heading: var(--colour-secondary);
    --pattern-colour: var(--colour-dark-tint);

    @if $compatible == 'true' {

      h1,
      h2,
      .h1,
      .h2 {
        color: #{$success};
      }

      .pattern__inner svg {
        stroke: #{$colour-dark-2-tint}!important;
      }
    }
  }

  .bg-secondary{
    --colour-heading: #{$colour-dark-2};
    
    .pattern__inner svg {
      opacity: 0.5;
    }

    @if $compatible == 'true' {
      h1,
      h2,
      .h1,
      .h2 {
        color: #{$colour-dark-2};
      }
    }
  }

  .bg-secondary-2[class*="bg-"][class*="bg-"]:not(.bg-primary):not(.bg-secondary):not(.bg-dark):not(.bg-dark-2):not(.container--product-cards),
  .bg-dark[class*="bg-"][class*="bg-"]:not(.bg-primary):not(.bg-secondary):not(.bg-dark-2):not(.bg-secondary-2):not(.container--product-cards),
  .bg-dark-2[class*="bg-"][class*="bg-"]:not(.bg-primary):not(.bg-secondary):not(.bg-dark):not(.bg-secondary-2):not(.container--product-cards) {
    --colour-heading: var(--colour-secondary);
    --colour-body: #FFFFFF;
    color: var(--colour-body);

    &:not(footer):not([class*="stroke-"]) .pattern__inner svg {
    
      
      @include var(stroke,--colour-dark-2-tint,!important);
      opacity: 0.5;
    }

    .btn-secondary[data-toggle] {
      @include btn-colours(--colour-black, --colour-secondary, $border:--colour-secondary, $state:'active');
    }

    @if $compatible == 'true' {
      color: #ffffff;

      h1,
      h2,
      .h1,
      .h2 {
        color: #{$success};
      }

      .pattern__inner svg {
        stroke: #{$colour-dark-2-tint}!important;
      }
    }
  }


  // Headings
  --heading-weight: 400;
  @if $compatible == 'true' {

    @include headers($includeClasses: 'true'){
      font-weight: 400;
    }
  }
}
