.pattern {
  position: relative;
  z-index: 1;
}

.pattern__inner {

  --pattern-scale: 1;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  pointer-events: none;
  overflow: hidden;
  margin: 0 !important;

  svg {
    fill: none;
    @include var(stroke,--pattern-colour);
    position: absolute;

    @include pattern-dimensions();

    top: 50%;
    left: 50%;
    z-index: -1;
    transform: translate(-50%, -50%) scale(var(--pattern-scale-adjust));
    transform-origin: 50% 50%;
    display: block;
    overflow: visible;
  }

  [class*="bg-"]:not(.bg-primary) & svg {

    opacity: 0.5;
  }

  //Possibly being removed
  .pattern--xs & {

    //--pattern-scale: 0.3;
    svg {

      @include pattern-dimensions(0.3);
    }
  }
  //Possibly being removed
  .pattern--sm & {

    //--pattern-scale: 0.5;
    svg {

      @include pattern-dimensions(0.5);
    }
  }

  .pattern--lg & {

    --pattern-scale: 2;
    svg {

      @include pattern-dimensions(2);
    }
  }

  .pattern--xl & {
    --pattern-scale: 2.5;
    svg {

      @include pattern-dimensions(2.5);
    }
  }
  
  .pattern--xxl & {
    --pattern-scale: 3;
    svg {

      @include pattern-dimensions(3);
    }
  }


  .pattern--top & {
    svg {
      top: 0;
      transform: translate(-50%, 0%);
    }
  }

  .pattern--bottom & {
    svg {
      top: 100%;
      transform: translate(-50%, -100%);
    }
  }

  .pattern--left & svg {
    left: 20%;
  }

  .pattern--right & svg {
    left: 85%;
  }

  .pattern--connect-top & svg {
    top: 0;
  }

  .pattern--connect-bottom & svg {
    top: 100%;
  }

  .pattern--connect-left & svg {
    left: 0;
  }
  .pattern--connect-right & svg {
    left: 100%;
  }

  .pattern--vertical & {
    svg {
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }

  .pattern--diagonal-left & svg {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  .pattern--diagonal-right & svg {
    transform: translate(-50%, -50%) rotate(-45deg);
    transform-origin: 50% 50%;
  }

  .pattern--push-up & svg {
    margin-top: rem(-100);
  }
  .pattern--push-left & svg {
    left: 25%;
  }
  
  @include media-breakpoint-up(md) {

    .pattern--md-push-left & svg {
      left: 25%;
    }
  }
}

@if $compatible == 'true' {
  
  body.ie .pattern--vertical .pattern__inner svg {
    
    transform: translate(-50%, -50%) rotate(91deg);
  }
}


