// Allow patterns within individual columns
.pattern[class*="col-"] {
  z-index: 3;
}

// Colour combinations 
@if $compatible == 'true' {
  .bg-primary {
    @include pattern-colour(--colour-primary-tint);
    .pattern__inner  svg {
      opacity: 1!important;
    }
  }

  .bg-secondary {
    @include pattern-colour(--colour-secondary-tint);
  }

  .bg-secondary-2 {
    @include pattern-colour(--colour-secondary-2-tint);
  }

  .bg-secondary-3 {
    @include pattern-colour(--colour-secondary-3-tint);
  }

  .bg-dark {
    @include pattern-colour(--colour-dark-tint);
  }

  .bg-dark-2 {
    @include pattern-colour(--colour-dark-2-tint);
  }
}

// Create the stroke classes
@each $color, $value in $theme-colors {

  .stroke-#{$color} .pattern__inner svg {

    @include var(stroke,--colour-#{$color});
    opacity: 1!important;
  }
}
@each $color, $value in $non-theme-colors {

  .stroke-#{$color} .pattern__inner svg {

    @include var(stroke,--colour-#{$color})
  }
}

.stroke-opaque .pattern__inner svg {
  opacity: 1!important;
}
