// #region Body Text
@include inline-text(){
  margin: 0;
  padding: 0;
	font-size: rem(16);
	line-height: rem(24);
  max-width: $max-content-width;
  word-wrap: break-word;
  overflow-wrap: break-word;
  padding-bottom: rem(48);
  //@include var(color,--colour-body);
  @include var(max-width,--max-content-width);
  @include var(font-family,--font-body);
  font-weight: 400;
  font-variation-settings: 'wght' 300;
}

p + p {
  margin-top:rem(-16);
}
// #endregion

// #region modifiers
strong,
.strong {
  font-weight: 900;

  @supports (font-variation-settings: normal) {
    font-weight: bold;
    font-variation-settings: 'wght' 600;
  }
}

em,
.em {
  @supports (font-variation-settings: normal) {
    font-variation-settings: 'slnt' -10;
  }
}

.lead {
  
  font-weight: bold;
  @supports (font-variation-settings: normal) {
    font-variation-settings: 'wght' 500;
  }
  font-size: rem(18);
  line-height: rem(28);
}

%small {
  font-size: rem(14);
	line-height: rem(24);
  padding-bottom: rem(48);
  @include var(font-family,--font-body);
  font-weight: 400;
  font-variation-settings: 'wght' 300;

  @include invert-colours(){
    color: inherit;
  }
}

.small,
.caveat {
  @extend %small;
} 

.small + .small,
.caveat + .caveat {
  margin-top: rem(-24);
}
// #endregion

// TODO: Do we really need the below? 
// #region Text alignment helpers

.text-center {

  @include inline-text(){
    margin-left: auto;
    margin-right: auto;
  }
  @include headers(){
    margin-left: auto;
    margin-right: auto;
  }
}

@include media-breakpoint-up(sm) {
  .text-sm-center  {
    @include inline-text(){
      margin-left: auto;
      margin-right: auto;
    }
    @include headers(){
      margin-left: auto;
      margin-right: auto;
    }
  }
  .text-sm-left  {
    @include inline-text(){
      margin-left: 0;
    }
    @include headers(){
      margin-left: 0;
    }
  }
}

@include media-breakpoint-up(md) {
  .text-md-center  {
    @include inline-text(){
      margin-left: auto;
      margin-right: auto;
    }
    @include headers(){
      margin-left: auto;
      margin-right: auto;
    }
  }
  .text-md-left  {
    @include inline-text(){
      margin-left: 0;
      margin-right: auto;
    }
    @include headers(){
      margin-left: 0;
      margin-right: auto;
    }
  }
}
// #endregion

// #region blockquote

blockquote {

  border-top: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);
  position: relative;
  @include var(color,--colour-primary);

  > * {
    @include var(color, --colour-body);
  }

  &:before{
    content: '';
    height: rem(48);
    width: rem(48);
    display: block;
    margin-bottom: rem(8);
    background-color: currentColor;
    
    @if $compatible == 'true' {
    
      background-image: escape-svg($quote-red-svg);
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-color: transparent!important;
    }
    @else {
      mask-image: var(--icon-quote);
      mask-size: 100% 100%;
      mask-repeat: no-repeat;
      mask-position: 0 0;
    
      -webkit-mask-image: var(--icon-quote);
      -webkit-mask-size: 100% 100%;
      -webkit-mask-repeat: no-repeat;
      -webkit-mask-position: 0 0;
    }
  }

  @if $compatible == 'true' {

    &.text-secondary-3:before {
      background-image: escape-svg($quote-grape-svg);
    }
  }

  p {
    
    font-size: rem(22);
    line-height: rem(32);
    padding-bottom: rem(32);
    letter-spacing: -0.2px;
    @include var(font-family,--font-heading);
  }
  
  p:first-child():before {
    content: open-quote;
  }
  p:nth-last-child(2):after {
    content: close-quote;
  }

  cite {
    @extend %heading;
    font-weight: bold;
    font-style: normal;
    font-size: rem(16);
    line-height: rem(24);
    padding-bottom: rem(32);

    @include media-breakpoint-up(sm) {

      font-size: rem(18);
    }
  }
  
  @include invert-colours() {
    
    &:before {
      @if $compatible == 'true' {
    
        background-image: escape-svg($quote-white-svg);
      }
      @else {
        @include var(background-color, --colour-body, !important);
      }
    }
  }

  @include media-breakpoint-up(sm) {

    &:before{
      height: rem(64);
      width: rem(64);
      margin-bottom: rem(16);
    }
    p {
      font-size: rem(28);
      line-height: rem(36);
    }
    cite {
      font-size: rem(18);
    }
  }

  // Utility fixes
  &:not(.blockquote--top){
  
    padding-left: rem(48 + 16);

    @include media-breakpoint-up(sm) {

      padding-left: rem(64 + 24);
    }

    &:before{
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &:not(.border-0){
      
    margin-bottom: rem(48);
    padding-top: rem(32);

    &:before{
      top: rem(32);
    }
  }

}

// #endregion