// #region Virgin Sans 
@font-face {
  font-family: VirginMoneySans;
  src:  url(../fonts/VirginMoneySans-Light.woff2) format("woff2"),
        url(../fonts/VirginMoneySans-Light.woff) format("woff");
  font-weight: 200;
  font-display: swap;
}

@font-face {
  font-family: VirginMoneySans;
  src:  url(../fonts/VirginMoneySans-Regular.woff2) format("woff2"),
        url(../fonts/VirginMoneySans-Regular.woff) format("woff");
        font-display: swap;
}
@font-face {
  font-family: VirginMoneySans;
  src:  url(../fonts/VirginMoneySans-Medium.woff2) format("woff2"),
        url(../fonts/VirginMoneySans-Medium.woff) format("woff");
  font-weight: 700;
  font-display: swap;
}
// #endregion

// #region Virgin Loop
@font-face {
  font-family: VirginMoneyLoop;
  src:  url(../fonts/VirginMoneyLoop-Light.woff2) format("woff2"),
        url(../fonts/VirginMoneyLoop-Light.woff) format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: VirginMoneyLoop;
  src:  url(../fonts/VirginMoneyLoop-Regular.woff2) format("woff2"),
        url(../fonts/VirginMoneyLoop-Regular.woff) format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
// #endregion

// #region Personality classes for Virgin Loop font
@each $key, $value in $personalities {

  .personality-#{$key} {
    --personality: var(--personality-#{$key});
  }
}
// #endregion

// #region Inter
// Variable font
@supports (font-variation-settings: normal) {

  /* Subset made using fonttools using the below command:
  // pyftsubset Inter.ttf --unicodes="U+0020-007F,U+00A0-00FF,U+0100-017F,U+2010-201F,U+2192,U+FF07,U+05F3,U+2022" --layout-features="" --flavor="woff2" --output-file="Inter-subset.woff2"
  // U+0020-007F = !"#$%&'()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[\]^_`abcdefghijklmnopqrstuvwxyz{|}~␡
  // U+00A0-00FF = ¡¢£¤¥¦§¨©ª«¬­®¯°±²³´µ¶·¸¹º»¼½¾¿00C0ÀÁÂÃÄÅÆÇÈÉÊËÌÍÎÏÐÑÒÓÔÕÖ×ØÙÚÛÜÝÞßàáâãäåæçèéêëìíîïðñòóôõö÷øùúûüýþÿ
  // U+0100-017F = ĀāĂăĄąĆćĈĉĊċČčĎďĐđĒēĔĕĖėĘęĚěĜĝĞğĠġĢģĤĥĦħĨĩĪīĬĭĮįİıĲĳĴĵĶķĸĹĺĻļĽľĿŀŁłŃńŅņŇňŉŊŋŌōŎŏŐőŒœŔŕŖŗŘřŚśŜŝŞşŠšŢţŤťŦŧŨũŪūŬŭŮůŰűŲųŴŵŶŷŸŹźŻżŽžſ
  // U+2010-201F = ‐‑‒–—―‖‗‘’‚‛“”„‟
  // U+2192      = →
  // U+FF07,U+05F3 Other apostraphes fixes 
  // U+2022      = Disc used for lists 
  */
  @font-face {
    font-family: 'Inter';
    font-display: swap;
    src: url(../fonts/Inter-subset.woff2) format('woff2');
    font-weight: 100 900;
    font-stretch: 25% 150%;
  }
  @font-face {
    font-family: 'Inter Full';
    font-display: swap;
    src: url(../fonts/Inter.var.woff2) format('woff2');
    font-weight: 100 900;
    font-stretch: 25% 150%;
  }
}

// Fallback
@font-face {
  font-family: 'Inter Static';
  font-weight: 400;
  font-display: swap;
  src:  url(../fonts/Inter-Light.woff2) format("woff2"),
        url(../fonts/Inter-Light.woff) format("woff");
}

@font-face {
  font-family: 'Inter Static';
  font-weight: 700;
  font-display: swap;
  src:  url(../fonts/Inter-Medium.woff2) format("woff2"),
        url(../fonts/Inter-Medium.woff) format("woff");
}

@font-face {
  font-family: 'Inter Static';
  font-weight: 900;
  font-display: swap;
  src:  url(../fonts/Inter-SemiBold.woff2) format("woff2"),
        url(../fonts/Inter-SemiBold.woff) format("woff");
}

// #endregion