.theme--giving {
  
  --colour-secondary: #C8F5F5;
  --colour-secondary-2: #E73B3B;
  --colour-secondary-tint: #ffffff;
  --colour-secondary-2-tint: #E10A0A;
  --pattern-colour: rgba(200,245,245,0.5);

  .default-colours {

    --colour-secondary: #C8F5F5;
    --colour-secondary-2: #E73B3B;
    --colour-secondary-tint: #ffffff;
    --colour-secondary-2-tint: #E10A0A;
  }

  --pattern-stroke-sm: #{(24/1.6)}px;
  --pattern-stroke-default: #{(32 / 1.6)}px;
  --pattern-stroke-lg: #{(28 / 1.6)}px;
  --pattern-stroke-xl: #{(26 / 1.6)}px;
  --pattern-scale-adjust: 1.6;


  .bg-secondary-2 .pattern__inner svg {
    opacity: 1;
  }

  :is(h3,h4,h5,h6) {
    color: var(--colour-heading);
  }

  @if $compatible == 'true' {

    h3,
    h4,
    h5,
    h6 {
      @include var(color,--colour-heading);
    }
  }
}