// #region basic link css 
a {
  @include var(color,--colour-link);

  // Remove the text decoration by default so that when we wrap a link around an image it doesn't look terrible
  cursor: pointer;
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    cursor: pointer;
    text-decoration: none;
  }

  // Remove the outline as this looks terrible in firefox and is only really need on the user focus' using the keyboard.
  &:hover,
  &:active {
    outline: none;
  }

  &:hover:not(:active),
  &:focus:not(:active) {
    @include var(color,--colour-hover);
    text-decoration: underline;
  }

  &:active  {
    @include var(color,--colour-active);
    text-decoration: underline;
  }

  &.selected,
  .js-enabled &[data-toggle='collapse']:not(.collapsed){
    @include var(color,--colour-selected);
  }

  &[data-icon] .svg__wrapper,
  &.data-icon .svg__wrapper{
    vertical-align: top;
  }

  .svg__wrapper {

    margin-left: rem(4);

    svg {
      stroke: currentColor;
      height: rem(16);
      width: rem(16);
    }
  }
  
  &[data-icon-left] .svg__wrapper,
  &.data-icon-left .svg__wrapper {
    margin-left: 0;
    margin-right: rem(4);
  }
}
// #endregion

// #region inline link
@include inline-text(){

  a,
  a:focus,
  a:hover,
  a:active,
  &.selected {
    text-decoration: underline;
    font-weight: 900;
    
    @supports (font-variation-settings: normal) {
      font-weight: bold;
      font-variation-settings: 'wght' 600;
    }
  }

  a:hover {

    @include var(color,--colour-hover);
  }

  a:focus {

    @include var(color,--colour-focus);
  }
  a:active {

    @include var(color,--colour-active);
  }

  &.selected {
    @include var(color,--colour-selected);
  }
  
  a[data-icon],
  a.data-icon{
    text-decoration: none;

    &:focus,
    &:hover,
    &:active,
    &.selcted {
      text-decoration: underline;
    }
  }

  a .svg__wrapper {
    display: inline-block;

    svg {
      height: 1em;
      width: 1em;
      vertical-align: baseline;
      display: inline-block;
      margin-right: 0.1em;
      margin-bottom: -0.2em;
      stroke: currentColor;
    }

    &--large svg {
      height: 1.5em;
      width: 1.5em;
      margin-top: -0.25em;
      margin-bottom: -0.4em;
      margin-right: 0.2em;
    }
  }

  a[disabled] {
    opacity: $btn-disabled-opacity;
    pointer-events: none;
  }
}

p {

  a[data-icon],
  a.data-icon{
    text-decoration: underline;
  }

  a .svg__wrapper {
    display: none !important;
  }
}
// #endregion