%looped {

  // Personality settings
  font-feature-settings: var(--personality);

  * {
    font-feature-settings: var(--personality);
  }
}

%heading {
  clear: both;
  @include var(font-weight,--heading-weight);
  @include var(font-family, --font-heading);
  margin-bottom: 0;
  width: 100%;
  display: block;
  max-width: $max-content-width;

  strong,
  .strong,
  b {
    font-weight: inherit;
  }
}

// #region Headings
h1,
.h1 {
  @extend %heading;
  @extend %looped;
  @include var(font-family,--font-loop);
  @include var(color,--colour-heading);
  
  font-size: rem(48);
  line-height: rem(56);
  padding-bottom: rem(48);
  letter-spacing: -0.75px;
  max-width: 100%;

  [aria-hidden="true"] {
    display: inherit!important;
  }

  @include media-breakpoint-up(sm) {
    font-size: rem(64);
    line-height: rem(72);
    padding-bottom: rem(64);
    letter-spacing: -1px;
  }
}

h2,
.h2 {
  @extend %heading;
  @extend %looped;
  @include var(font-family,--font-loop);
  @include var(color,--colour-heading);

	font-size: rem(36);
  letter-spacing: -0.75px;
	line-height: rem(44);
  padding-bottom: rem(40);
  max-width: 100%;

  @include media-breakpoint-up(sm) {

    font-size: rem(48);
    line-height: rem(56);
    padding-bottom: rem(48);
  }
}

h3,
.h3 {
  @extend %heading;
	font-size: rem(28);
  letter-spacing: -0.25px;
	line-height: rem(36);
  padding-bottom: rem(40);
  color: inherit;

  @include media-breakpoint-up(sm) {

    font-size: rem(32);
    letter-spacing: -0.5px;
    line-height: rem(40);
    padding-bottom: rem(40);
  }
}

h4,
.h4 {
  
  @extend %heading;
	font-size: rem(24);
  letter-spacing: -0.25px;
	line-height: rem(32);
  padding-bottom: rem(32);
  color: inherit;
}

h5,
.h5 {
  @extend %heading;
  font-size: rem(20);
  letter-spacing: 0px;
  line-height: rem(28);
  padding-bottom: rem(32);
  color: inherit;

  @include media-breakpoint-up(sm) {

    letter-spacing: -0.25px;
  }
}

h6,
.h6 {
  
  @extend %heading;
  font-weight: bold;
  font-size: rem(16);
  line-height: rem(24);
  padding-bottom: rem(24);
  color: inherit;

  @include media-breakpoint-up(sm) {

    font-size: rem(18);
  }
}
// #endregion

// #region ABBR fix
abbr {
  text-decoration: none!important;
  font-feature-settings: "ss18"!important;
  text-transform: uppercase;
}
// #endregion

// #region Display Heading 
@include media-breakpoint-up(md) {

  .display-1 {
    font-size: rem(96);
    letter-spacing: -2px;
    line-height: rem(112);
    @include var(font-family,--font-loop);
    padding-bottom: rem(64);
    max-width: 100%;

    // Personality settings
    font-feature-settings: var(--personality);

    * {
      font-feature-settings: var(--personality);
    }
  }

  .display-2 {
    font-size: rem(80);
    letter-spacing: -1.5px;
    line-height: rem(88);
    @include var(font-family,--font-loop);
    padding-bottom: rem(64);
    max-width: 100%;

    // Personality settings
    font-feature-settings: var(--personality);

    * {
      font-feature-settings: var(--personality);
    }
  }

  .display-3 {
    font-size: rem(72);
    letter-spacing: -1.5px;
    line-height: rem(80);
    @include var(font-family,--font-loop);
    padding-bottom: rem(64);
    max-width: 100%;

    // Personality settings
    font-feature-settings: var(--personality);

    * {
      font-feature-settings: var(--personality);
    }
  }

  .display-4 {
    font-size: rem(64);
    letter-spacing: -1px;
    line-height: rem(72);
    @include var(font-family,--font-loop);
    padding-bottom: rem(64);
    max-width: 100%;
  }
}
// #endregion

// #region Straplines 

.strapline {
  @include var(font-family, --font-heading);
  font-weight: 400;
  font-variation-settings: 'wght' 600;
  @include var(color,  --colour-dark-2);
  margin-top: rem(-16);
  font-size: rem(18);
	line-height: rem(28);
  padding-bottom: rem(40);

  @include invert-colours(){
    color: inherit;
  }
}

h1 + .strapline,
.h1 + .strapline,
.strapline.strapline--h1  {
  font-size: rem(22);
	line-height: rem(32);
  padding-bottom: rem(48);
}


@include media-breakpoint-up(sm) {

  .strapline {
    font-size: rem(21);
    letter-spacing: -0.25px;
    line-height: rem(28);
    padding-bottom: rem(48);
  }

  h1 + .strapline,
  .h1 + .strapline,
  .strapline.strapline--h1 {
    margin-top: rem(-32);
    font-size: rem(24);
    letter-spacing: -0.25px;
    line-height: rem(28);
    padding-bottom: rem(64);
  }
}


@include media-breakpoint-up(md) {

  .display-1,
  .display-2, 
  .display-3, 
  .display-4 {

    & + .strapline {
      font-size: rem(28);
      line-height: rem(36);
    }
  }

  .strapline.strapline--display {

    font-size: rem(28);
    line-height: rem(36);
  }
}
// #endregion

// #region Numeric

.numeric {
  color: inherit;

  @include var(font-family,--font-heading);

  &:not(h6):not(.h6) .suffix {
    font-size: 0.75em;
  }
}

// #endregion
