.btn {

  //imported bootstrap buttons button config
  display: inline-block;
  text-align: inherit;
  vertical-align: middle;
  user-select: none;
  text-decoration: none;
  width: auto;
  position: relative;
  border: 1px solid transparent;
  border-radius: rem(0);
  cursor: pointer;
  transition: none;
  box-shadow: none!important;
  white-space: normal;
  @include var(font-family,--font-heading);
  font-weight: bold;
  font-size: rem(16);
  line-height: rem(24);
  padding: rem(12) rem(24);
  margin-bottom: rem(48);
  
  &:focus-visible {
    outline: none;
  }

  // Hover and focus states
  &:hover:not(:active),
  &:focus:not(:active),
  &:active {

    text-decoration: none;
    box-shadow: none;
  }

  &[disabled] {
    pointer-events: none;
    cursor: auto;
    opacity: $btn-disabled-opacity;
  }

  @include media-breakpoint-up(sm) {
    
    font-size: rem(18);
    padding: rem(20) rem(32);
    margin-bottom: rem(64);
  }
}

.btn-primary,
.btn-secondary,
.btn-tertiary {
  @include btn-colours-states();
}

// Primary button ****************************************************************************
.btn-primary{

  @include btn-colours(--colour-white, --colour-primary, --colour-primary, $state:'default');
  @include btn-colours(--colour-white, --colour-primary-light, --colour-primary-light, $state:'hover');
  @include btn-colours(--colour-white, --colour-primary-tint, --colour-primary-tint, $state:'active');

  // Invert colours when on a dark background
  @include invert-colours($exclude:":not(.container--cta):not(.container--product-cards)"){

    @include btn-colours(--colour-primary, --colour-white, $border:--colour-white, $state:'default');
    @include btn-colours(--colour-primary-tint, --colour-light, $border:--colour-light, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, $border:--colour-border, $state:'active');
  }

  &.btn-invert {

    @include var(color,--colour-body);
    @include btn-colours(--colour-primary, --colour-white, $border:--colour-white, $state:'default');
    @include btn-colours(--colour-primary-tint, --colour-light, $border:--colour-light, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, $border:--colour-border, $state:'active');
  }
}

// Secondary button ****************************************************************************
.btn-secondary {

  @include btn-colours(--colour-primary, transparent, currentColor, $state:'default');
  @include btn-colours(--colour-white, --colour-primary, --colour-primary, $state:'hover');
  @include btn-colours(--colour-white, --colour-primary-tint, --colour-primary-tint, $state:'active');

  @include invert-colours($exclude:":not(.container--product-cards)"){

    border-width: 2px;
    @include btn-colours(--colour-white, transparent, --colour-white, $state:'default');
    @include btn-colours(--colour-black, --colour-white, --colour-white, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, --colour-border, $state:'active');
  }
  &.btn-invert {

    border-width: 2px;
    @include btn-colours(--colour-white, transparent, --colour-white, $state:'default');
    @include btn-colours(--colour-black, --colour-white, --colour-white, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, --colour-border, $state:'active');
  }
  
  &[class*="text-"]:hover,
  &[class*="text-"]:focus,
  &[class*="text-"]:active {
      
    color: var(--btn-colour-hover)!important;

    @if $compatible == 'true' {
  
      color: white!important;
    }
  }
}

// Secondary button ****************************************************************************
.btn-tertiary {

  @include btn-colours(--colour-muted, transparent, currentColor, $state:'default');
  @include btn-colours(--colour-white, --colour-muted, --colour-muted, $state:'hover');
  @include btn-colours(--colour-white, --colour-dark-2-tint, --colour-dark-2-tint, $state:'active');

  @include invert-colours($exclude:":not(.container--product-cards)"){

    border-width: 2px;
    @include btn-colours(--colour-white, transparent, --colour-white, $state:'default');
    @include btn-colours(--colour-black, --colour-light, --colour-light, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, --colour-border, $state:'active');
  }
  &.btn-invert {

    border-width: 2px;
    @include btn-colours(--colour-white, transparent, --colour-white, $state:'default');
    @include btn-colours(--colour-black, --colour-light, --colour-light, $state:'hover');
    @include btn-colours(--colour-black, --colour-border, --colour-border, $state:'active');
  }
}



// Basic buttons *****************************************************************************************
.btn-link {
  background: none;
  padding-left: 0;
  padding-right: 0;
  background: none!important;
  text-align: inherit;
  @include var(color,--colour-body);
  border: none;
  padding: 0 0 rem(4) 0;
  margin: 0 0 rem(32) 0;
  min-height: rem(28);

  text-decoration: none;

  p > & {
    display: inline;
  }
  
  &:not(.border-0) {
      
    text-decoration: underline;
    text-underline-offset: 0.5rem;
  }

  @include media-breakpoint-up(sm) {
    
    font-size: rem(16);
    padding: 0 0 rem(4) 0;
    margin: 0 0 rem(32) 0;
    min-height: rem(28);
  }

  &:hover:not(:active),
  &:focus:not(:active),
  &:active {

    &:not(.border-0) {

      text-decoration: underline;
      text-decoration-thickness: 2px;
    }
  }

  
  &[data-toggle="collapse"]:not([data-icon]) {
    
    text-align:left;
    position: relative;
    padding-left: rem(32);
    z-index: 1;

    &:after {
      left: rem(32);
      width: calc(100% - #{rem(32)});
    }

    &:before {
      content: "";
      
      position: absolute;
      top: 0;
      left: 0;
      height: rem(24);
      width: rem(24);
      transition: transform 0.35s;
  
      @if $compatible == 'true' {
  
        background-image: escape-svg($plus-svg);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-color: transparent!important;
      }
      @else {
        mask-image: var(--icon-plus);
        mask-size: 100% 100%;
        mask-repeat: no-repeat;
        mask-position: 0 0;
      
        -webkit-mask-image: var(--icon-plus);
        -webkit-mask-size: 100% 100%;
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-position: 0 0;
        background-color: currentColor;
      }

      @include invert-colours-compatible(){

        background-image: escape-svg($plus-white-svg);
      }
    }
    @if $compatible == 'true' {
      &:hover:before,
      &:focus:before {
      
        background-image: escape-svg($plus-red-svg);
      }
    }
      

    &:not(.collapsed):before {
      transform: rotate(45deg);
      transform-origin: 50% 50%;

      @if $compatible == 'true' {
  
        background-image: escape-svg($plus-red-svg);
      }

      @include invert-colours-compatible(){

        background-image: escape-svg($plus-white-svg);
      }

    }


    + .collapse,
    + .collapsing {
      
      margin-bottom: $spacing-xs;
      padding-left: rem(32);
      margin-top: rem(-16);
    }
  }


  &[data-toggle="collapse"][data-icon-right] {

    padding-left: 0;
    padding-right: rem(32);

    &:before {
      left: auto;
      right: 0;
    }

    &:after {
      left: 0;
    }
  }
}

// Fix the positioning of CTA links
p + .btn-link {
  margin-top: rem(-16);
}

.btn-link + p {
  margin-top: rem(16);
}

// List of links *************************************************************************************************
.list-links {

  @extend .list-unstyled;
  
  > li > a {
    @extend .btn;
    padding: rem(24) 0;
    margin: 0;
    width: 100%;
    min-height: 100%;
    min-width: auto;
    border: none;
    border-bottom: 1px solid currentColor;
    @include var(border-color,--colour-border);
  
    @include media-breakpoint-up(sm) {
      
      font-size: rem(16);
      padding: rem(24) 0;
      margin: 0;
    }

    &:hover:not(:active),
    &:focus:not(:active),
    &:active,
    .js-enabled &[data-toggle='collapse']:not(.collapsed)  {
      text-decoration: underline;
      @include var(border-color,--colour-border);
    }
  }

  > li:first-child > a {

    border-top: 1px solid currentColor;
    @include var(border-color,--colour-border);
  }

  > li > span ~ a {
    margin-bottom: rem(32);
    border-top: 1px solid currentColor;
    @include var(border-color,--colour-border);
  }

  @include media-breakpoint-up(sm) {
    &[class*="list-columns-sm"] {
      > li:first-child > a {

        border-top: none;
      }
    }
  }
}

// Button Modifiers **********************************************************************************************

// Icons
.btn {
  
  .svg__wrapper {

    display: inline-block;
    height: rem(16);
    width: rem(16);

    padding-left: 0;
    margin-left: rem(-8);
    margin-right: rem(16);
    margin-top: rem(4);
    vertical-align: top;

    svg{

      height: 100%;
      width: 100%;
      display: block;
    }


    @include media-breakpoint-up(sm) {
      height: rem(24);
      width: rem(24);
      margin-top: 0;
      margin-left: rem(-12);
    }
  }

  &.btn--icon-right .svg__wrapper{

    margin-left: rem(16);
    margin-right: rem(-8);

    @include media-breakpoint-up(sm) {

      margin-left: rem(16);
      margin-right: rem(-12);
    }
  }


}

.list-links > li > a {

  &[data-icon]:not([data-icon-right]):not(.btn--icon-right){

    padding-left: rem(24); 

    .svg__wrapper {
      
      margin-left: rem(-24);
    }

    @include media-breakpoint-up(sm) {

      padding-left: rem(32); 

      .svg__wrapper {
        
        margin-left: rem(-32);
      }
    }
  }
    
  .svg__wrapper {
    margin-left: 0;
    margin-right: rem(8);
  }

  &[data-icon-right] .svg__wrapper,
  &.btn--icon-right .svg__wrapper {

    margin-right: 0;
    margin-left: rem(8);
  }

}


// Fix the styling for btn-links with icons. Only works when css variables is supported

.btn-link {

  .svg__wrapper {
    margin-left: 0;
    height: rem(16 + 10);
    width: rem(16 + 8);
    padding-bottom: rem(10);
    margin-bottom: rem(-10);
    margin-right: 0;
    padding-right: rem(8);
    z-index: 1;
    position: relative;

    @include media-breakpoint-up(sm) {

      height: rem(24 + 10);
      width: rem(24 + 8);
    }
  }

  &.btn--icon-right .svg__wrapper{

    margin-left: 0;
    margin-right: 0;
    padding-left: rem(8);
    padding-right: 0;
  }

  
}

// Button Groups **********************************************************************************************
.btn-group:not(.btn-group--inline){

  padding-bottom: rem(16);
  
  .btn:not(.btn-link) {

    display: block;
    width: 100%;
    margin-bottom: rem(16);
    text-align: center;
  }
  
  @include media-breakpoint-up(sm) {
    display: inline-flex;
    flex-wrap: wrap;
    width: calc(100% + #{rem(16)});
    margin-left: rem(-8);
    margin-right: rem(-8);
    padding-bottom: rem(32);

    .btn {
      order: 1;
      width: auto!important;
      margin-left: rem(8);
      margin-right: rem(8);
    }
    .btn-primary {
      order: 3;
    }
    .btn-secondary {
      order: 2;
    }
  }

}

.btn-group--inline {

  padding-bottom: rem(40 - 8);

  @include media-breakpoint-up(sm) {

    padding-bottom: rem(48 - 8);
  }
  @include media-breakpoint-up(md) {

    padding-bottom: rem(64 - 8);
  }

  .btn:not(.btn-link) {

    margin-bottom: rem(8);
    margin-right: rem(4);
  }
}

// Button with badges **********************************************************************************************
/* 
For these buttons we use px units so that we can guarantee we are matching their brand guidelines
*/
.btn-badge {
  padding: 0;
  margin-right: 0.5rem;
  background-repeat: no-repeat;
  background-position: 0 0;
  border: none;
  border-radius: 0;
  background-size: contain;
  text-indent: -100vw;

  height: 40px;
  overflow: hidden;

  // Apple
  &.btn-badge--apple-store {

    width: 120px;
    background-image: url(../images/apple-store.svg);
  }

  &.btn-badge--apple-pay {

    width: 61px;
    background-image: url(../images/apple-pay.svg);
  }

  // Google
  &.btn-badge--google-play {

    width: 133px;
    background-image: url(../images/google-play.png);
  }
  &.btn-badge--google-pay {

    width: 63px;
    background-image: url(../images/google-pay.png);
  }
} 

// Button meta dat
.btn__meta {
  display: block;
  font-weight: normal;

  a:not(:hover):not(:focus):not(:active) & {

    @include var(color,--colour-muted);
  }
}

// Button icon only **********************************************************************************************
.btn-icon-only {
  padding: rem(16);
  @include var(color, --colour-white);
  @include var(background, --colour-dark);

  &:hover:not(:active) {
    @include var(color, --colour-white);
    @include var(background, --colour-dark-tint);
  }

  &:active {
    @include var(color, --colour-white);
    @include var(background, --colour-dark);
  }

  .svg__wrapper {
    margin: 0;
    vertical-align: middle;
    width: rem(32);
    height: rem(32);
  }
}
