/* List out the utilities from bootstrap 4 here */


.full-width-panel{
  width: calc(100% + #{$grid-gutter-width * 4});
  margin-left: -$grid-gutter-width * 2;
}
 
.limit-width{
  max-width: $max-content-width;
}
 
.limit-width-10cols{
  max-width: $max-content-10col;
}

.max-width-none {
  max-width: none!important;
}

//JS Helpers
.js-enabled .js-hide {
  display: none;
}

body:not(.js-enabled) .js-show {
  display: none;
}

[aria-hidden="true"]{
  display: none!important;
}

.mh-0 {
  min-height: 0!important;
}

body.ie .d-ie-block {
  display: block!important;
}