@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false' !default;
$print: 'false' !default;
@import "../../functions";

// #region banner
.banner {

  position: relative;
  z-index: 1;
  overflow: hidden;

  .container {
    padding-bottom: rem(16);
    min-height: rem(200);
    position: relative;
    margin: 0;
    background-color: rgba(0,0,0,0); // For accessiblity on windows high contrast mode
    content-visibility: unset!important;
  }

  .pattern__inner {

    display: block; 
    overflow: visible;
  }


  &:not(.banner--split):not(.pattern--vertical):not(.pattern--diagonal-left):not(.pattern--diagonal-right){
    .pattern__inner {
      svg {
        transform: translate(-50%, -50%) rotate(90deg);
        --pattern-offset: 1800px;
        --pattern-animation-speed: 6s;
      }
    }
  }

  &:not(.banner--split):not(.pattern--center):not(.pattern--left):not(.pattern--right):not(.pattern--connect-left):not(.pattern--connect-right){
    .pattern__inner {
      svg {
        left: 85%;
      }
    }
  }



  @include media-breakpoint-up(sm) {

    .container {

      padding-bottom: rem(32);
      min-height: rem(240);

      > *:not(.pattern__inner) {
        max-width: rem(800)
      }
    }
  }

  @include media-breakpoint-up(md) {

    max-width: rem($device-md-width);
    margin: 0 auto;

    .container {

      padding-top: rem(96);
      padding-bottom: rem(32);
      min-height: rem(320);
    }
  }

  .banner__bg {
    display: none;

    img {
      max-width: none;
      position: absolute;
      top: 50%;
      left: 50%;
      height: 100%;
      width: 100%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }

  .banner__anchor {
    display: none;
  }
}
@if $compatible == 'true' {
  
  body.ie.zoom .banner:not(.banner--split):not(.pattern--vertical):not(.pattern--diagonal-left):not(.pattern--diagonal-right) .pattern__inner svg {
    
    transform: translate(-50%, -50%) rotate(91deg);
  }
}
// #endregion

// #region Banner light H1 colour override
.banner.bg-light h1 {
  @include var(color,--colour-primary);
}
// #endregion

// #region Banner split
.banner--split {

  .container {

    min-height: rem(320);
  }

  .pattern__inner {

    display: block; 

    svg {
      transform: translate(-50%, -50%) rotate(-45deg);
      left: 50%;
      top: 50%;
      @include pattern-dimensions(2);
      --pattern-scale: 2;
      --pattern-offset: 2250px;

      --pattern-animation-speed: 10s;
    }
  }

  .banner__bg {
    display: block;
    min-height: rem(210);
    position: relative;
  }

  @include media-breakpoint-up(sm) {

    .container {

      min-height: rem(400);
      padding-top: rem(80);
    }

    .banner__bg {
      
      min-height: rem(432);
    }
  }

  @include media-breakpoint-up(md) {

    display: flex;

    .container {

      min-height: rem(640);
      padding-right: rem(110);
      width: 50%;
      padding-top: rem(120);
      padding-bottom: rem(80);

      > *:not(.pattern__inner) {
        max-width: none;
      }
    }

    .banner__bg {
      
      min-height: rem(640);
      width: 50%;
    }
  }

  &.img--square .banner__bg {
      
    @include media-breakpoint-up(sm) {
      min-height: rem(640);
    }
  }

  &.pattern--connect-bottom {
    .pattern__inner {

      --pattern-scale: 2.5;

      svg {
        
        margin-top: rem(-100);
        top: 100%;
        @include pattern-dimensions(2.5);
      }
    }
  }
}

.pattern--banner-speed {
  .pattern__inner {

    svg {
      --pattern-offset: 2250px;
      --pattern-animation-speed: 10s;
    }
  }
}
// #endregion

// #region Banner article
.banner--article {
  display: flex;
  flex-flow: column-reverse;

  .badge {
    position: absolute;
    left: 0;
    bottom: 100%;
    z-index: 2;
    margin: 0;
  }

  .container {
    padding-top: rem(32);
    padding-bottom: rem(32);
  }

  @include media-breakpoint-up(sm) {

    .container {
      padding-top: rem(64);
      padding-bottom: rem(32);
    }
  }
  @include media-breakpoint-up(md) {

    flex-flow: row;

    .badge {
      position: static;
      margin-bottom: rem(16);
    }

    .container {
      padding-top: rem(96);
      padding-bottom: rem(32);
    }
  }
}

// #endregion


// #region Banner full
.banner--full {
  --gradient-pos: 63%;
  --gradient-deg: 0deg;

  .container {

    min-height: rem(640);
    z-index: 2;
    padding-bottom: rem(32);
  }

  .pattern__inner {

    display: none;
  }

  .banner__bg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      background: linear-gradient(var(--gradient-deg), rgba(255,255,255,0) 0%, rgba(0,0,0,0.3) var(--gradient-pos));
      @if $compatible == 'true' {
        background: linear-gradient(0deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.3) 63%);
      }
    }
  }

  @include media-breakpoint-up(sm) {

    --gradient-pos: 67%;

    .container {

      min-height: rem(800);
      padding-top: rem(80);
      padding-bottom: rem(40);
    }

    @if $compatible == 'true' {
      .banner__bg:before {
      background: linear-gradient(0deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.3) 67%);
      }
    }
  }

  @include media-breakpoint-up(md) {

    --gradient-pos: 50%;
    --gradient-deg: 270deg;

    .container {

      min-height: rem(640);
      padding-right: rem(8);
      width: 50%;
      padding-top: rem(120);
      padding-bottom: rem(80);
    }

    @if $compatible == 'true' {
      .banner__bg:before {
      background: linear-gradient(270deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.3) 50%);
      }
    }


    .banner__anchor {
      display: block;

      position: absolute;
      left: 50%;
      bottom: 0;
      z-index: 10;
      margin-left: rem(-48);
      width: rem(96);
      height: rem(144);
      background: rgba(0,0,0,0.5);

      .svg__wrapper {
        position: absolute;
        top: rem(24);
        left: 50%;
        margin-left: rem(-24);
        height: rem(48);
        width: rem(48);

        svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
// #endregion
