@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false' !default;
$print: 'false' !default;
@import "../../functions";

.title-bar {
  padding-top: rem(32);
  padding-bottom: 0;

  @include media-breakpoint-up(md) {
    
    padding-top: rem(40);
  }

  .brand{
    margin: rem(-8) rem(-8) rem(32 - 8) rem(-8);

    min-height: rem(48 + 8 + 8);
    min-width: rem(238 + 8 + 8);

    svg {
      min-height: rem(48);
      min-width: rem(238);
    }
  }

  &__title {
    padding-bottom: rem(32);
    display: block;

    @include media-breakpoint-up(md) {
    
      padding-bottom: rem(40);
    }
  }


  // Button close 
  &__close {
    text-indent: -300vw;
    overflow: hidden;
    height: rem(32);
    width: rem(32);
    display: block;
    position: relative;
    border: rem(2) solid currentColor;
    @include var(color,--colour-white);
    border-radius: 50%;
    position: absolute;
    top: rem(32);
    right: $container-padding;


    .svg__wrapper svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      height: rem(22);
      width: rem(22);

    }

    @include media-breakpoint-up(sm) {
      right: $container-sm-padding;
      height: rem(48);
      width: rem(48);
        
      .svg__wrapper svg {
        height: rem(32);
        width: rem(32);
      }
    }

    @include media-breakpoint-up(md) {
      right: $container-md-padding;
      top: rem(40);
    }
  }
}