@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false' !default;
$print: 'false' !default;
@import "../../functions";

// #region Navbar
.navbar {

  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);
  @include var(background,--colour-white);
  z-index: 10;
  position: relative;
  width: 100%;
  padding-left: rem(24);
  padding-right: rem(24);

  @include media-breakpoint-up(sm) {

    padding-left: rem(32);
    padding-right: rem(32);
  }

  @include media-breakpoint-up(md) {

    padding-left: 0;
    padding-right: 0;
  }

  > .container-md {
    padding-top: rem(24);
    padding-bottom: rem(24);
    display: flex;
    flex-flow: row nowrap;
    position: relative;
  }

  .brand {
    padding: 0;
    margin-left: rem(-8);
    margin-right: auto;
    min-height: rem(32);
  
    @include media-breakpoint-up(md) {
      min-height: rem(48);
      padding-top: rem(4);
      padding-bottom: rem(4);
      margin-right: rem(48 - 8);
    }
  }
}
//#endregion

// #region toggle buttons for mobile and desktop
.navbar-toggler {

  background: none;
  border: none;
  margin-left: rem(24);
  text-decoration: none!important;

  .svg__wrapper {

    svg {
      width: rem(32);
      height: rem(32);
    }
  }

  .svg__wrapper--selected {
    display: none;
  }

  body.js-enabled &:not(.collapsed){
    .svg__wrapper--selected {
      display: inline-block;
    }
    .svg__wrapper--selected + .svg__wrapper {
      display: none;
    }
  }

  .navbar-toggler__label {
    display: none;
  }

  @include media-breakpoint-up(sm) {
    white-space: nowrap;
    margin-left: rem(32);


    .svg__wrapper {
      vertical-align: middle;
      margin-top: rem(-4);
      margin-left: rem(8);
      
      svg {
        width: rem(24);
        height: rem(24);
      }
    }

    .navbar-toggler__label {
      display: inline-block;
      line-height: rem(32);
      font-weight: bold;
      font-variation-settings: 'wght' 600;
    }
  }

  @include media-breakpoint-up(md) {
    display: none;
  }
}
// #endregion

// #region Fix the navbar to top of the page
body > .navbar {
  position: fixed;

  & + * {
    margin-top: rem(80);
  }

  @include media-breakpoint-up(md) {

    position: relative;

    & + * {
      margin-top: 0;
    }
  }
}
// #endregion

// #region Menu on mobile and tablet
.navbar > .container-md {

  > .collapse,
  > .collapsing {
    position: absolute;
    top: 100%;
    right: 0;
    margin-right: rem(-24);
    width: rem(375);
    height: calc(100vh - #{rem(80)})!important;
    border-top: 1px solid currentColor;
    @include var(border-color,--colour-border);
    padding: 0 rem(24);
    display: block;
    box-shadow: -2px 2px 4px rgba(0,0,0,0.2);
    z-index: 1;
    @include var(background,--colour-light);

    @include media-breakpoint-up(sm) {

      margin-right: rem(-32);
      padding: 0 rem(32);
    }
    @include media-breakpoint-up(md) {
      margin-right: 0;
      padding: 0 $container-md-padding 0 rem(32);
      width: calc(#{rem(32)} + #{$container-md-padding} + #{rem(320)});
    }
  }
  
  > .collapse:not(.show) {

    transform: translate(calc(100% + #{rem(6)}),0);
    -ms-transform: translate(105%,0);
  }

  > .collapsing  {
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }

  > .collapsing.opening,
  > .collapse:target {

    animation: 0.5s ease-out slide-left forwards;
    z-index: 5;
  }
  
  > .collapsing.closing {

    animation: 0.5s ease-out slide-left-out forwards;
    z-index: 5;
  }
  

  > .collapse.show {

    overflow-y: auto;
    
  }


  .btn-secondary {
    margin-bottom: rem(48);
    display: inline-block;
    line-height: rem(24);
    padding: rem(12) rem(24);
    font-size: rem(16);

    .svg__wrapper {
      height: rem(24);
      width: rem(24);
      margin-right: rem(8);
      margin-top: 0;
    }

    .svg__wrapper--selected {
      display: none;
    }
  
    body.js-enabled &:not(.collapsed){
      .svg__wrapper--selected {
        display: inline-block;
      }
      .svg__wrapper--selected + .svg__wrapper {
        display: none;
      }
    }
  }
  
  @include media-breakpoint-up(md) {
  &:after {
    content: "";
    position: absolute;
    z-index: 99;
    top: calc(100% + 1px);
    left: 100%;
    height: 100vh;
    width: 100vw;
    @include var(background,--colour-white);
  }
  }

}

.list-nav {

  padding-top: rem(16);
  padding-bottom: rem(64);

  > li {

    padding-bottom: 0;
    padding-left: 0;
    border-bottom: 1px solid currentColor;
    @include var(border-color,--colour-border);

    &:before {
      display: none;
    }
  }

  > li > a {

    @include var(font-family,--font-heading);
    font-weight: bold;
    font-size: rem(16);
    line-height: rem(24);
    text-decoration: none;
    margin: 0;
    width: auto;
    min-width: auto;
    border: none;
    margin: rem(24) 0;
    display: inline-block;

    @include media-breakpoint-up(sm) {
      
      font-size: rem(16);
    }


    &:hover:not(:active) {
      color: inherit;
    }

    &:focus,
    &:hover,
    &:active,
    &.selected,
    &[data-toggle]:not(.collapsed)  {
      text-decoration: none;
      border-bottom: 1px solid currentColor;
      margin-bottom: calc(#{rem(24)} - 1px);
    }
    
    &[data-toggle]:not(.collapsed):not(:hover):not(:focus):not(:active)  {
      border-color: transparent!important;
    }
  }
}
.navbar__cta-title {
  padding-top: rem(32);
  padding-bottom: rem(16);
  max-width: rem(320);
}
// #endregion

// #region .menu bar along the top on desktop
@include media-breakpoint-up(md) {

  .navbar  > .container-md > .collapse.menu {
    position: static;
    height: rem(48)!important;
    width: auto;
    box-shadow: none;
    transform: none;
    padding: 0;
    flex: 1 1 100%;
    max-width: 100%;
    white-space: nowrap;
    display: flex;
    justify-content: space-around;
    border-top: none;
    overflow: visible;
    z-index: inherit;
    @include var(background,--colour-white);

    > .list-nav {
      padding-top: 0;
      margin-bottom: 0;
      padding-bottom: 0;
      margin-left: auto;

      > li {

        display: inline-block;
        margin-right: rem(48);
        border: none;

        > a {
          margin: rem(12) 0;
        }
      }
    }


    .btn-secondary {
      margin-bottom: 0;
      display: inline-block;
      line-height: rem(24);
      padding: rem(12) rem(24);
      font-size: rem(16);

      .svg__wrapper {
        height: rem(24);
        width: rem(24);
        margin-right: rem(8);
        margin-top: 0;
      }
    }
  }
}
// #endregion

// #region Sign in pages
.navbar {

  .btn-cta-pages.btn {
    display: none;
  }
  @include media-breakpoint-up(md) {
    .btn-cta-pages.btn + a.btn {
      display: none !important;
    }
  }
}
// #endregion

// #region Mega menu
.navbar--dual {

  > .container-md > .collapse.menu {
    @include var(background,--colour-white);


    @include media-breakpoint-up(md) {
      > .list-nav {
        margin-left: 0;
        margin-right: auto;
      }
      > .list-nav + .list-nav {
        margin-left: auto;
        margin-right: 0;
      }
    }
  } 

  .menu > .list-nav:first-child {
    margin-left: rem(-24);
    margin-right: rem(-24);
    padding-bottom: 0;

    @include media-breakpoint-up(sm) {
      
      margin-left: rem(-32);
      margin-right: rem(-32);
    }
    @include media-breakpoint-up(md) {
      margin: 0;
    }

    > li {
      position: relative;

      @include media-breakpoint-up(md) {
        position: static;
      }
    }

    > li > a {

      margin-left: rem(32);
      margin-right: rem(32);


      &[data-toggle]{
        
        @include plus-icon();
        display: inline-block;
        width: auto;
        position: static;

        &:after {
          top: rem(18);
          right: rem(24);
        }
      }

      
      @include media-breakpoint-up(md) {
        &[data-toggle] {
          position: relative;
          padding-right: rem(32);
        }
        
        &[data-toggle]:before {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          width:rem(32);
          height: rem(32);
          background: var(--bg-colour);
        }
        &[data-toggle]:after {
          top: 0;
          left: auto;
          right: 0;
          height: em(24);
          width: em(24);

          @if $compatible == 'true' {

            background-image: escape-svg($chevron-down-svg);
          }
          @else {
            mask-image: var(--icon-chevron-down);
            -webkit-mask-image: var(--icon-chevron-down);
          }

        }

        &[data-toggle]:not(.collapsed):after {
          transform: none;
        }
      }
    }

    .navbar__categories .navbar__categories__inner {

      @include var(background,--colour-light);
      padding: rem(8) rem(32);
      border-top: 1px solid currentColor;
      border-color: var(--colour-border);

      > a[data-toggle]{
        
        @include plus-icon();
        margin: rem(16) 0 rem(16) rem(32);
        line-height: rem(24);
        display: inline-block;
        text-decoration: none;
        @include var(font-family,--font-heading);

        &:hover:not(:active) {
          color: inherit;
        }

        &:hover,
        &:focus,
        &:active,
        &:not(.collapsed) {

          
          border-bottom: 1px solid currentColor;
          margin-bottom: calc(#{rem(16)} - 1px);
        }

        &:after {
          left: 0;
          right: auto;
          width: rem(24);
          height: rem(24);
          top: calc(50% - #{rem(24/2)});
          margin-left: rem(-32);
        }
      }

      .collapse:not(.show) {
        display: block;
        height: 0;

        > * {
          display: none;
        } 
      }

      .list-nav:not(.subnav__landing-page):not(.subnav__featured-pages) {
        padding-bottom: rem(32);
      }
    }

    > li > .subnav {

      padding-left: rem(32);
      padding-right: rem(32);
    }
  }

  .menu > .list-nav:not(:first-child) {

    li {
      border-bottom: none;
    }
  }

  .menu .list-nav {
    padding-bottom: rem(16);

    @include media-breakpoint-up(md) {
      padding-bottom: rem(16);
    }
  }

  .menu .list-nav .list-nav {
    padding-top: 0;
    padding-bottom: 0;
    padding-left: rem(32);

    @include media-breakpoint-up(md) {
      padding-left: 0;
      padding-bottom: 0;
    }
  }
}
// #endregion

// #region Desktop Categories menu
@include media-breakpoint-up(md) {
  .navbar--dual .menu > .list-nav:first-child .navbar__categories {
    transition: none!important;
    position: absolute!important;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0;
    z-index: 100;
    border-top: 1px solid currentColor;
    @include var(border-color,--colour-border);
    max-height: calc(100vh - #{rem(88)});


    > .container-md {
      padding-top: rem(0);
      padding-bottom: rem(0);
      text-align: center;
      @include var(background, --colour-light);
      box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);


      .navbar__categories__inner {
        padding: 0;
        margin-left: rem(-16);
        margin-right: rem(-16);
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        border-top: none;

        .subnav.show {

          box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);
        }
        .subnav.show .container-md {

          position: relative;

          &:after {
            content: "";
            position: absolute;
            top: 0;
            left: #{$container-md-padding};
            width: calc(100% - #{$container-md-padding} - #{$container-md-padding});
          border-top: 1px solid currentColor;
          @include var(border-color,--colour-border);
          }
        }
      }
      .navbar__categories__inner > a {
        display: inline-block;
        margin: 0 rem(16);
        padding: rem(28) 0;

        &:after {
          display: none;
        }

        &[data-toggle]:hover,
        &[data-toggle]:focus,
        &[data-toggle]:active,
        &[data-toggle]:not(.collapsed) {
          border-bottom: 3px solid currentColor;
          padding-bottom: calc(#{rem(28)} - 3px);
          margin-bottom: 0;
          z-index: 5;
        }

        &[data-toggle]:not(.collapsed) {
          pointer-events: none;
        }
      }
    }
  }
}
// #endregion

// #region Desktop Subnav menu
@include media-breakpoint-up(md) {
  .subnav {
    text-align: left;
    transition: none!important;
    position: absolute!important;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 3;
    @include var(background,--colour-light);
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);
    padding-left: 0!important;
    padding-left: 0!important;
    overflow-y: auto;
    max-height: calc(100vh - #{rem(88 + 112)});
    white-space: normal;

    .container-md {
      padding-top: rem(28);
    }

    .subnav__landing-page {
      margin: 0;
      max-width: none;

      li {
        border: none;
      }
    }

    .subnav__landing-page li a {
      clear: both;
      font-weight: $headings-font-weight;
      @include var(font-family, --font-heading);
      margin-bottom: 0;
      width: auto;
      display: inline-block;
      font-size: rem(32);
      letter-spacing: -0.5px;
      line-height: rem(40);
      padding-bottom: 0;
      margin-bottom: calc(#{rem(40)} - 1px);

      &:not(:hover):not(:focus):not(:active){
        border: none;
        margin-bottom: rem(40);
      }

      position: relative;
      margin-right: rem(48);

      &:after {

        content: "";
        position: absolute;
        top: rem(8);
        left: 100%;
        margin-left: rem(6);
        height: rem(32);
        width: rem(32);
        transition: transform 0.25s ease-out;

        @if $compatible == 'true' {

          background-image: escape-svg($chevron-svg);
          background-size: 100% 100%;
          background-repeat: no-repeat;
          background-position: 0 0;
          background-color: transparent!important;
        }
        @else {
          mask-image: var(--icon-chevron-right);
          mask-size: 100% 100%;
          mask-repeat: no-repeat;
          mask-position: 0 0;
        
          -webkit-mask-image: var(--icon-chevron-right);
          -webkit-mask-size: 100% 100%;
          -webkit-mask-repeat: no-repeat;
          -webkit-mask-position: 0 0;
          background-color: currentColor;
        }
      }
    }

    .list-nav.subnav__featured-pages {
      margin-bottom: rem(32);
      padding-right: rem(16);
    }
    .list-nav:not(.subnav__featured-pages){

      padding-right: rem(16);

      li {

        border: none;
      }
    }

    // Cards
    .card {
      text-decoration: none!important;
      position: relative;
      padding-left: rem(60);
      padding-bottom: 0;

      .card-body {
        padding-top: rem(24);
        padding-right: rem(16);
      }

      .card-title {
        padding-bottom: rem(16);
      }
    }

    .card-image-nav {
      display: block;
      position: absolute;
      top: rem(16);
      left: rem(16);
      height: rem(60);
      width: rem(60);
    }
  }
}
// #endregion

// #region Non-js fallback
body:not(.js-enabled){

  .navbar > .container-md > .collapse:target {
    overflow-y: auto;
  }

  @media(max-width: 62em) {
    .navbar--dual .menu > .list-nav:first-child > li > a[data-toggle] {

      pointer-events: none;
    }


    .navbar--dual .menu > .list-nav:first-child .navbar__categories {
      height: auto!important;
  
      .navbar__categories__inner > a[data-toggle] {
  
        pointer-events: none;
      }
    }

    .subnav {
      height: auto!important;
    }
  }

  @include media-breakpoint-up(md) {
    .navbar--dual .menu > .list-nav:first-child .navbar__categories {

      overflow-y: auto;
    }
    .navbar--dual .menu > .list-nav:first-child .navbar__categories > .container-md .navbar__categories__inner {
      display: block;
    }
    .navbar--dual .menu > .list-nav:first-child .navbar__categories > .container-md .navbar__categories__inner > a {
      display: none;
    }
    .navbar--dual .menu > .list-nav:first-child .navbar__categories > .container-md .navbar__categories__inner > .collapse {
      display: block;
      height: auto;
      position: static!important;
    }
  }
}
// #endregion
