// Two column layout fix
#content.col-md-8 > .container:not(:first-child){
  padding-top: 0;
}
#content.col-md-8 > .container:not(:last-child){
  padding-bottom: 0;
}
#content.col-md-8 > .container:last-child{
  margin-bottom: 0;
}

#content.col-md-8 > .container--statement {
  margin-bottom: rem(48);
}

@include media-breakpoint-up(md) {

    #content.col-md-8 > .container--breakout {
        margin-left: rem(-120);
        width: rem(1440);
    }
}

// Prevent too much gray background.
.bg-light:not(.content-list) + .bg-light:not(.bg-light-important){

  @include var(background-color,--colour-white,!important);
  --bg-colour: var(--colour-white);
}
// But allow card decks to have bg-light backgrounds
.bg-light.container--deck:not(.content-list) + .bg-light.container--deck {

  @include var(background-color,--colour-light,!important);
  --bg-colour: var(--colour-light);
}

// Add some spacing for the footer
#content:not([class*="col"]) {

  z-index: 1;
  position: relative;

  > *:not([class*="bg-"]):not(.container--split):not(.tabs__container--carousel):last-child {
    padding-bottom: rem(64);
      
    @include media-breakpoint-up(sm) {

      padding-bottom: rem(64 + 16);
    } 
    @include media-breakpoint-up(md) {

      padding-bottom: rem(80 + 32);
    }
  }

  > .bg-dark-2:last-child {
    margin-bottom: rem(64);

    @include media-breakpoint-up(md) {
  
      margin-bottom: rem(80);
    }
  }
}

.container.bg-dark-2 + footer {
  margin-top: rem(64);

  @include media-breakpoint-up(md) {

    margin-top: rem(80);
  }
}

@include media-breakpoint-up(md) {

  .container-md + footer {
    margin-top: rem(80 - 32);
  }
}

// Remove the top padding if the container doesn't have a background and is next to a container without a background
#content {

  > .container:not(.container--split):not([class*="bg-"]) + .container:not([class*="bg-"]) {
    padding-top: 0;
  }
}

// #region Article
article > .banner--split.pattern--connect-bottom {

  .pattern__inner svg{
    margin-top: rem(-248);
    margin-left: rem(-48);
  }
}

article > .banner + .container.pattern {

  z-index: -1;

  .pattern__inner {
    min-height: rem(640);

    svg {

      margin-top: rem(-248);
      margin-left: rem(-48);
    }
  }
}

article > .list-social__container {

  z-index: 1;
}

article > .container:not(.container--split):not([class*="bg-"]) + .container:not([class*="bg-"]){

  padding-top: 0;
  padding-bottom: 0;
  position: relative;
}

// Video article
article > .container:first-child + .container.pattern {

  padding-bottom: 0;
  
  .pattern__inner {
    display: none;
  }
}
article > .container:first-child ~ .list-social__container {

  display: none;
}


@include media-breakpoint-up(sm) {

  article > .container:not([class*="bg-"]):not(.container--split):not(.tabs__container--carousel):last-child {

    padding-bottom: rem(16)!important;
  }
}

@include media-breakpoint-up(md) {

  article > .banner--split.pattern--connect-bottom {
  
    .pattern__inner svg{
      margin-top: rem(-160);
    }
  }
  article > .banner + .container.pattern {

    content-visibility: unset;
    
    .pattern__inner svg{
      left: 25%;
      margin-top: rem(-160);
    }
  }
  article > .list-social__container {

    position: relative;
    z-index: 2;
    content-visibility: unset;
    
    .list-social {
      position: absolute;
      top: 100%;
      left: rem(120);
    }
  }
  article > .container:not(.container--default):not(.container--split):not([class*="bg-"]) {

    padding-left: rem(320);
    padding-right: rem(320);
  }
  article > .container:not([class*="bg-"]):not(.container--split):not(.tabs__container--carousel):last-child {

    padding-bottom: rem(32)!important;
  }
}
// #endregion
