@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false' !default;
$print: 'false' !default;
@import "../../functions";

// #region container
.container {

  margin-left: auto;
  margin-right: auto;
  padding: $container-all-padding;
  
  body:not([data-no-animate]):not(.no-animate) &:not(.inview) {
    content-visibility: auto; /* this is the thing that delays painting */
    contain-intrinsic-size: 1px 500px; /* this is the guess at the height of the content, and also saying width doesn't matter */
  }

  .container {
    content-visiblity: unset!important; /* We only want to delay the painting of top level containers */
  }

  @include media-breakpoint-up(sm) {
    padding: $container-all-sm-padding;
    max-width: rem($container-max-width);
  }
  @include media-breakpoint-up(md) {

    padding: $container-all-md-padding;
  } 

  // Fix the clearing for containers
  &:before {
    display: table;
    clear: both;
    content: "";
  }
  &:after {
    display: table;
    clear: both;
    content: "";
  }

  //Remove padding from nested containers
  .container:not(.container--expand) {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-top: 0;
  }

  .container--expand {

    margin-left: -#{$container-padding};
    margin-right: -#{$container-padding};
    width: auto;
   // $container-sm-padding: rem(48);
   // $container-md-padding: rem(120);
    @include media-breakpoint-up(sm) {
      margin-left: -#{$container-sm-padding};
      margin-right: -#{$container-sm-padding};
    }
    @include media-breakpoint-up(md) {

      margin-left: -#{$container-md-padding};
      margin-right: -#{$container-md-padding};
    } 
  }
}
// #endregion

// #region Container-md
.container-sm, .container-md {
  padding-right: 0;
  padding-left: 0;
}
@include media-breakpoint-up(md) {

  .container-md {

    padding: $container-all-md-padding;
    max-width: rem($container-max-width);
    margin-right: auto;
    margin-left: auto;

    .container:not(.container--expand) {
      padding-left: 0;
      padding-right: 0;
      padding-bottom: 0;
      padding-top: 0;
    }
  }
}
// #endregion

// #region Split container
.container--split {
  padding: 0!important;

  > .row {
    margin: 0!important;

    > [class*="col"] {

      min-width: 100%!important;
      flex: 0 0 100%;
      max-width: 100%;
      padding: $container-all-padding;
      //min-height: rem(320);
      position: relative;
      z-index: 1;

      &:not(.pattern--connect-top):not(.pattern--bottom) .pattern__inner svg {
        top: rem(320/2);

        @include media-breakpoint-up(sm) {
          top: rem(480/2);
        }
        @include media-breakpoint-up(md) {  
          top: rem(640/2);
        }
      }

      &.contains-img {

        min-height: rem(210);

        @include media-breakpoint-up(sm) {
          min-height: rem(432);
        }
      }

      @include media-breakpoint-up(sm) {
        padding: $container-all-sm-padding;
        //min-height: rem(480);
      }
      @include media-breakpoint-up(md) {
        min-width: 50%!important;
        flex: 0 0 50%;
        max-width: 50%;
        padding: $container-all-md-padding;
        min-height: rem(640);

        &:nth-child(odd){
          padding-right: rem(110);
        }
        &:nth-child(even){
          padding-left: rem(110);
        }
      }

      &.contains-logo {

        padding: rem(16);

        > img:not(:first-child){
          transform: scale(0.5);
          transform-origin: top left;;
        }

        @include media-breakpoint-up(sm) {
          
          padding: rem(32);

          > img:not(:first-child){
            transform: none;
          }
        }
      }

      picture {
        position: static;
      }

      > img:first-child,
      > picture:first-child img,
      > .pattern__inner:first-child + img,
      > .pattern__inner:first-child + picture img {
        max-width: none;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        height: 100%;
        width: 100%;
        transform: translate(-50%, -50%);
        object-fit: cover;
        margin: 0;
      }

      // Image utilities 
      &.img--square {

        min-height: rem(320);
        
        img {
          object-fit: contain!important;
          object-position: 50% 0%;
        }

        @include media-breakpoint-up(sm) {
          
          min-height: rem(480);
        }
        @include media-breakpoint-up(md) {
          
          padding: 0!important;
        }
      }

      &.img--position-bottom {

        img {
          object-position: 50% 100%!important;
        }
      }

      &.img--overlap-left img {

        @include media-breakpoint-up(md) {
          
          transform: translate(-100%, -50%)!important;
        }
      }
      
      > .youtube-embed:first-child,      
      &.youtube-embed__overlay > .youtube-embed {
        max-width: none;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        height: 100%;
        width: 100%;
        transform: translate(-50%, -50%);
        object-fit: cover;
        margin: 0;

        .youtube-embed__inner {
          padding-top: 0;
          height: 100%;
        }
        img {

          height: 100%;
        }
      }
      &.youtube-embed__overlay > .youtube-embed {
          
        z-index: 1;

        img {
          display: none;
        }
      }
    }
  }
}

.container--split-landscape,
.container--split-short {
  > .row {

    > [class*="col"] {
      min-height: 0;
    }

    &.contains-img {

      @include media-breakpoint-up(md) {
        min-height: rem(480);
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .container--split-inline {

    padding: $container-all-md-padding!important;

    .col {
      padding: rem(64) rem(56) rem(64 - 36) rem(56)!important;
    }
  }
}

// #endregion

// #region Split container Grid 
.container--split-grid {
  > .row > .col:first-child {

    min-height: rem(320);

    @include media-breakpoint-up(sm) {
      min-height: rem(400);
    }
    @include media-breakpoint-up(md) {
      min-height: rem(480);
    }
  }
  > .row {
    > .col:nth-child(1){
      
      max-width: 100%;
      min-width: 100%;
      flex-basis: 100%;

      .pattern__inner svg {
        top: 100%!important;
        margin-top: rem(-100);
        left: 25%;
      }
    }

    > .col:nth-child(3){
      
      .pattern__inner svg {
        top: 0!important;
        margin-top: rem(-100);
      }
    }
  }
  @supports (display: grid) {

    @include media-breakpoint-up(md) {
      
      > .row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: min-content min-content;
        grid-template-areas:
          "col1 col2"
          "col3 col2";

        > .col {

          max-width: 100%;
        }
        > .col:nth-child(1){
          grid-area: col1;

          .pattern__inner svg {
            left: 50%;
          }
        }
        > .col:nth-child(2){
          grid-area: col2;
        }
        > .col:nth-child(3){
          grid-area: col3;
        }
      }
    }
  }
}
// #endregion

// #region form container
.container--form {

  @include media-breakpoint-up(sm) {
    padding: rem(64) rem(106) rem(16) rem(106);
  }
  @include media-breakpoint-up(md) {

    padding: rem(80) rem(323) rem(32) rem(323);
  } 

}
// #endregion

// #region CTA container
.container--cta {

  padding-bottom: rem(192);
  min-height: rem(320);

  &[class*="bg-"]:not([class*="stroke-"]) .pattern__inner svg {
    
    @include var(stroke,--colour-primary,!important);
    opacity: 1;
  }

  @include media-breakpoint-up(sm) {

    padding-bottom: rem(16);
    padding-right: rem(280);
    min-height: rem(440);

    &.pattern--bottom .pattern__inner {

      svg {
        transform: translate(-50%, -50%) rotate(90deg);
        top: 55%;
        left: 85%;

      }
    }
  }

  @include media-breakpoint-up(md) {

    padding-top: rem(96);
    padding-bottom: rem(32);
    padding-right: rem(520);
    min-height: rem(480);
  }
}


@if $compatible == 'true' {
  .container--cta {

    @include media-breakpoint-up(sm) {
  
      .pattern__inner {
  
        svg {
          transform: translate(-50%, -50%) rotate(91deg);
        }
      }
    }
  }
}
// #endregion

// #region Statement container
.container--statement {
  
  padding-top: rem(24);

  > * {
    font-weight: bold;
    text-align: center;
    padding-left: rem(24);
    padding-right: rem(24);
    padding-bottom: rem(16);
    max-width: 100%;
  }

  > *:first-child {
    
    padding-top: rem(24);
    border-top: 2px solid currentColor;
  }
  > *:last-child {
    
    padding-bottom: rem(24);
    border-bottom: 2px solid currentColor;
  }
  
  @include media-breakpoint-up(sm) {
  
    > *:first-child {
      
      padding-top: rem(32);
    }
    > *:last-child {
      
      padding-bottom: rem(32);
    }
  }

  &[class*="bg-"]:not(.bg-secondary-2):not(.bg-secondary-3):not(.bg-secondary-4):not(.bg-light) {

    padding-top: rem(32);
    padding-bottom: rem(32);

    > *:first-child {
      padding-top: 0;
      border-top: none;
    }
    > *:last-child {
      
      padding-bottom: 0;
      border-bottom: none;
    }

    @include media-breakpoint-up(sm) {
      padding-top: rem(48);
      padding-bottom: rem(48);
    }
  }
}

.container .container.container--statement,
.container--statement {

  padding-bottom: rem(32);

  @include media-breakpoint-up(sm) {
  
    padding-bottom: rem(48);
  }

  @include media-breakpoint-up(md) {
  
    padding-bottom: rem(64);
  }
}

// #endregion

// #region Utility classes
@include media-breakpoint-up(md) {
  .container--limit-width {

    padding-right: rem(520);
  }
}
// #endregion

