// Horizontal Line breaks ************************************************************************
hr, .hr {
  margin-top: rem(8);
  margin-bottom: rem(48);
  border: 0;
  @include var(color,--colour-black);
  border-bottom: 1px solid currentColor;
  opacity: 0.2;
  
  &:after {
    display: table;
    clear: both;
    content: "";
  }

  &.hr--thick {
    border-bottom: 2px solid currentColor;
  }

  &.hr--clear {
    margin: 0;
    border-bottom: none;
    height: 0;
  }
}

