.badge {

  @include var(background,--colour-primary);
  @include var(font-family,--font-body);
  @include var(color,--colour-black);
  font-weight: bold;
  font-variation-settings: 'wght' 600;
  padding: rem(10) rem(12);
  font-size: rem(10);
  line-height: 1em;
  text-transform: uppercase;
  vertical-align: middle;
  display: inline-block;
  margin-bottom: rem(16);

  &:not(.bg-secondary-2):not(.bg-secondary-4):not(.bg-light){

    @include var(color,--colour-white);
  }


  @include media-breakpoint-up(sm) { 

    &:not(.badge--small) {
        
      font-size: rem(12);
      padding: rem(12) rem(16);
    }
  }

}