.author {
  position: relative;
  padding-left: rem(48 + 16);

  &__img {
    height: rem(48);
    width: rem(48);
    margin-right: rem(16);
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;

    img {
      object-fit: cover;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }

  .h6 {
    padding-bottom: rem(16);
  }

  .small {
    display: block;
    padding-bottom: rem(48);
  }

  @include media-breakpoint-up(sm) {

    padding-left: rem(64 + 16);

    &__img {
      height: rem(64);
      width: rem(64);
    }
  }

  &--bio {
    border-top: 2px solid currentColor;
    border-bottom: 2px solid currentColor;
    @include var(border-color,--colour-border);
    padding-top: rem(32);
    margin-bottom: rem(32);

    .author__img {
      top: rem(32);
    }

    @include media-breakpoint-up(sm) {

      padding-top: rem(48);
      margin-bottom: rem(48);

      .author__img {
        top: rem(48);
      }
    }
  }
}