/* SVGS */
.svg__wrapper{

  --svg-stroke-width: 2px;
  --svg-size: #{rem(24)};

  vertical-align: middle;
  display: inline-block;

  svg {
    
    @if $compatible == 'true' {
      width: rem(24);
      height: rem(24);
    }
    @else {
      width: var(--svg-size);
      height: var(--svg-size);
    }
    
    fill: none;
    @include var (stroke, --colour-primary);
    stroke: currentColor;
    stroke-width: 2px;
    stroke-width: var(--svg-stroke-width);
    stroke-linejoin: round;
  }

  svg + svg {
    display: none!important;
  }
  
  &--alt svg:first-of-type {
    display: none!important;
  }

  &--alt svg:first-of-type + svg {
    display: block!important;
  }

  &--lg {

    --svg-stroke-width: 1px;
    --svg-size: #{rem(48)};
    
    svg {
      @if $compatible == 'true' {
        width: rem(48);
        height: rem(48);
      }
      @else {
        width: var(--svg-size);
        height: var(--svg-size);
      }
    }
  }

  &--xl {

    --svg-stroke-width: 1.5px;
    --svg-size: #{rem(96)};
    
    svg {
      @if $compatible == 'true' {
        width: rem(96);
        height: rem(96);
      }
      @else {
        width: var(--svg-size);
        height: var(--svg-size);
      }
    }
  }
}

@include inline-text(){

  .svg__wrapper{
    vertical-align: text-bottom;
    display: inline-block;
    margin-right: 0.4em;

    svg {

      width: 1em;
      height: 1em;

    }

    &--lg svg {

      width: rem(24);
      height: rem(24);
    }
  }
}

.contains-svg {

  padding-left: 1.4em;
  position: relative;

  .svg__wrapper{
    position: absolute;
    top: 0;
    left: 0;
    margin-top: -0.1em;
  }
}