/*
#Remove the responsive classes for the spacing utilities

The spacing utilities are useful but could be mis-used. The spacing of elements should be defined via the elements/components CSS.
But at times tweaks/fixes to spacing needs to be made and can prevent the need of duplicate CSS to be created.
However our spacing rules should be consistant across devices and we shouldn't allow device specific 'fixes' to be made
Removing the resposive classes aves around 8kbs
*/
$utilities: map-merge(
  $utilities,
  (
    // Padding
    "padding": map-merge(
      map-get($utilities, "padding"),
      (
        responsive: false
      ),
    ),
    "padding-x": map-merge(
      map-get($utilities, "padding-x"),
      (
        responsive: false
      ),
    ),
    "padding-y": map-merge(
      map-get($utilities, "padding-y"),
      (
        responsive: false
      ),
    ),
    "padding-top": map-merge(
      map-get($utilities, "padding-top"),
      (
        responsive: false
      ),
    ),
    "padding-end": map-merge(
      map-get($utilities, "padding-end"),
      (
        responsive: false
      ),
    ),
    "padding-bottom": map-merge(
      map-get($utilities, "padding-bottom"),
      (
        responsive: false
      ),
    ),
    "padding-start": map-merge(
      map-get($utilities, "padding-start"),
      (
        responsive: false
      ),
    ),
    // Bootstrap 4 compatible padding classes 
    "padding-left": (
      responsive: false,
      property: padding-left,
      class: pl,
      values: map-merge($spacers, (auto: auto))
    ),
    "padding-right": (
      responsive: false,
      property: padding-right,
      class: pr,
      values: map-merge($spacers, (auto: auto))
    ),
    // Margin
    "margin": map-merge(
      map-get($utilities, "margin"),
      (
        responsive: false
      ),
    ),
    "margin-x": map-merge(
      map-get($utilities, "margin-x"),
      (
        responsive: false
      ),
    ),
    "margin-y": map-merge(
      map-get($utilities, "margin-y"),
      (
        responsive: false
      ),
    ),
    "margin-top": map-merge(
      map-get($utilities, "margin-top"),
      (
        responsive: false
      ),
    ),
    "margin-end": map-merge(
      map-get($utilities, "margin-end"),
      (
        responsive: false
      ),
    ),
    "margin-bottom": map-merge(
      map-get($utilities, "margin-bottom"),
      (
        responsive: false
      ),
    ),
    "margin-start": map-merge(
      map-get($utilities, "margin-start"),
      (
        responsive: false
      ),
    ),
    // Bootstrap 4 compatible margin classes 
    "margin-left": (
      responsive: false,
      property: margin-left,
      class: ml,
      values: map-merge($spacers, (auto: auto))
    ),
    "margin-right": (
      responsive: false,
      property: margin-right,
      class: mr,
      values: map-merge($spacers, (auto: auto))
    ),
    // Gap
    "gap": map-merge(
      map-get($utilities, "gap"),
      (
        responsive: false
      ),
    ),
  )
);

/*
# Remove Negative margins

Allowing negative margins will make controlling spacing difficult and could lead to broken or bizarre visuals
*/
$utilities: map-merge(
  $utilities,
  (
    "negative-margin": null,
    "negative-margin-x": null,
    "negative-margin-y": null,
    "negative-margin-top": null,
    "negative-margin-end": null,
    "negative-margin-bottom": null,
    "negative-margin-start": null,
  )
);
