// Images
img {

  max-width: 100%;
  @include type-spacing('margin-bottom');
}


figure {

  @include type-spacing('margin-bottom');

  img {
    margin-bottom: 0;
  }

  figcaption {
    font-size: rem(14);
    line-height: rem(24);
    padding-bottom: 0;
    padding-top: rem(16);
    @include var(font-family,--font-body);
    @include var(color,--colour-muted);
    font-weight: 400;
  }
}

.figure--caption-overlay {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0,0,0,0.00) 50%, rgba(0,0,0,0.80) 100%);
  }


  figcaption {

    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: rem(16);
    @include var(color,--colour-white);
  }
}

// Can be applied on image and on figure
.img--rounded {
  border-radius: 50%;
  overflow: hidden;
}


.img--square,
.img--portrait {

  width: 100%;
  padding-top: 100%;
  position: relative;

  img {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:not(.figure--caption-overlay ) figcaption {

    display: none;
  }
}

.img--portrait {

  padding-top: 133.33334%;
}

// Youtube
.youtube-embed {

  display: block;
  width: 100%;
  @include type-spacing('margin-bottom');
  position: relative;


  .youtube-embed__inner {

    display: block;
    width: 100%;
    padding-top: 56.25%;
    position: relative;
    overflow: hidden;
    outline: none!important;
  }
  
  a,
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: auto;
    transform: translate(-50%,-50%);
    object-fit: cover;
    z-index: 1;
  }

}

/* Button */
  
.youtube-embed__btn {

  position: absolute;
  top: 50%;
  left: 50%;
  width: rem(80);
  height: rem(80);
  border-radius: 50%;
  transform: translate(-50%,-50%);
  z-index: 3;
  background: rgba(30,35,50,0.75);

  .svg__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    height: rem(80 - 16);
    width: rem(80 - 16);
    margin:0;
    transform: translate(-50%,-50%);

    svg {
      stroke: white;
      stroke-width: 0.75px;
      height: 100%;
      width: 100%;
    }
  }

  @include media-breakpoint-up(sm) {

    width: rem(160);
    height: rem(160);

    .svg__wrapper {
      height: rem(160 - 32);
      width: rem(160 - 32);

      svg {

        stroke-width: 0.5px;
      }
    }
  }

  a:hover &,
  a:focus & {
    @include var(border-color, --colour-primary);
    @include var(background, --colour-primary);
  }

  a:active & {
    @include var(border-color, --colour-active);
    @include var(background, --colour-active);
  }
}



.modal__inner {
  .youtube-embed {
    max-width: 100%
  }
}



// Playlist
.playlist {

  width: 100%;
  max-width: rem(320);
  height: rem(390);
  @include type-spacing('margin-bottom');

  @include media-breakpoint-up(sm) {
    max-width: rem(490);
    height: rem(570);
  }

  iframe {
    width: 100%;
    height: 100%;
  }
}